import Reflux from 'reflux'

const actions = Reflux.createActions([
  "goToHome",
  "userClick",
  "loadFeeds",
  "login",
  "signUp",
  "closeModal",
  "view_module",
  "question",
  "answer",
  "upvote",
  "downvote",
  "getUserLogin"
])
export default actions;


